import bgImg from "../img_temp/header.png"
import wordArt1 from "../img_temp/section-happy.png"
import wordArt2 from "../img_temp/section-messages.png"
import wordArt3 from "../img_temp/section-rewind.png"
import wordArt4 from "../img_temp/section-quilt.png"

import '../styles/stylesCover.css'

export const ViewCover = () => {

    return (
        <div id='viewCover'>
            ViewCover Component
            <div>

                <img id='cover_1' src={bgImg}/>
                <img className='wordArt'  id='wordArt1' src={wordArt1}/>

                <img  className='wordArt'  id='wordArt2' src={wordArt2}/>
                <img  className='wordArt'  id='wordArt3' src={wordArt3}/>
                <img  className='wordArt'  id='wordArt4' src={wordArt4}/>

            </div>

            {/*<img src={require('../img_temp/header.png')} />*/}


        </div>
    )
}
